.close-oops {
  stroke: #fff;
  width: 15px;
  height: 15px;
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.backdrop-oops {
  z-index: 1000;
  background-color: #2e2f42b3;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  pointer-events: none;
  opacity: 0;
