.slider {
  cursor: pointer;
  filter: drop-shadow(0 0 6px #6b6969);
  background: linear-gradient(#000, #003);
  border-radius: 30px;
  justify-content: space-between;
  align-items: center;
  height: 32px;
  padding: 6px;
  transition: all .4s;
  display: flex;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  background: linear-gradient(74deg, #000, #003, #888);
  border-radius: 20px;
  width: 1.4em;
  height: 1.4em;
  transition: all .4s;
  animation: .3s linear anima1;
  position: absolute;
  bottom: .3em;
  left: .3em;
}

@keyframes anima1 {
  0% {
    transform: translateX(1.5em);
  }

  80% {
    transform: translateX(-.3em);
  }

  100% {
    transform: translateX(0);
  }
}

.switch input:checked + .slider:before {
  background: linear-gradient(90deg, #f47622, #fff);
  animation: .3s linear anima;
  transform: translateX(1.5em);
  box-shadow: 0 0 10px #000000b3;
}

.switch input:checked + .slider {
  background: linear-gradient(232deg, #ffc226 9.4%, #f84119 91.91%);
}

@keyframes anima {
  0% {
    transform: translateX(0);
  }

  80% {
    transform: translateX(1.6em);
  }

  100% {
    transform: translateX(1.4em);
  }
}

